@use "../base/variables" as v;
@use "../base/mixins" as m;
@use "../base/globales";

.video {
    position: relative;
    max-height: 70rem;
    min-height: 50rem;
    overflow: hidden;

    .overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        background: rgb(165,3,255);
        background: linear-gradient(270deg, rgba(167,3,255,0.5) 0%, rgba(255,0,110,0.3) 100%);
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    video {
        width: auto;
        display: block;
        @include m.tablet {
            width: 100%;
        }
       
    }

    .contenido-video {
        z-index: 10;
        h2 {
            color: v.$blanco;
            text-transform: uppercase;
            font-size: 7rem;
            margin: 0;
        }
        p {
            background-color: v.$rosa;
            display: inline-block;
            padding: 1rem 2rem;
            color: v.$blanco;
            font-weight: 700;
            font-size: 2.4rem;
        }
    }
}