@use "../base/variables" as v;
@use "../base/mixins" as m;

.header {
    background: v.$rosa;
    @include m.tablet {
        &.fijo {
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .3rem #333;
        }
    
    }
    h1 {
        color: v.$blanco;
        text-align: center;
        @include m.tablet {
            margin: 0;
        }
        
    }
    .contenido-header {
        padding: 2rem;
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
}
.navegacion-principal {
    @include m.tablet {
        display: flex;
        gap: 2rem;
    }
    @include m.telefono {
       
    }
    
    a {
        @include m.tablet {
            margin: 0;
        }
        display: block;
        color: v.$blanco;
        font-size: 2.4rem;
        text-align: center;
        margin-bottom: 1rem;
        &:hover {
            color: v.$amarillo;
        }
    }
}

