@use "../base/variables" as v;
@use "../base/globales" as g;
@use "../base/mixins" as m;

.boletos{
    padding: 5rem 0;
    h3 {
        text-align: center;
        color: v.$blanco;
        margin-bottom: 2rem;
    }
    .boletos-contenedor {
        @include m.tablet {
            @include m.grid(2, 5rem);
        }
    }
    
    /*.contenedor-boletos {
        @include m.tablet {
            @include m.grid(2, 5rem);
        }
    }*/
    
}


.pase {
    color: v.$negro;
    &-header,
    &-footer {
        padding: .5rem;
        p {
        text-align: center;
        color: v.$blanco;
        margin: 0;
        }
    }
    
    &-header {
        background-color: v.$rosa;
        p {
            text-transform: uppercase;
            font-size: 1.6rem;
        }
        
    }
    &-body {
        background-color: v.$blanco;
        ul {
            @include m.resetearLista;
            li{
                padding: .5rem;
                border-bottom: 1px solid #e1e1e1;
                &:nth-child(odd){
                    background-color: v.$blanco;
                    
                }
            }
            
        }
        
    }
    &-footer {
        background-color: v.$morado;
        p {
            font-size: 2.4rem;
            font-weight: 700;
        }
        
    }
    
}
.nombre-pase {
    text-align: center;
    font-size: 2.4rem;
    color: v.$blanco;
}